'use client';

import { useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';

// تعريف نوع بيانات الخدمة
export interface ServiceCardProps {
  id: string;
  title: string;
  description: string;
  image: string;
  category: string;
  price: number;
  provider?: {
    id: string;
    name: string;
    image?: string;
  };
  rating?: number;
  reviewsCount?: number;
}

export default function ServiceCard({ id, title, description, image, category, price, provider, rating, reviewsCount }: ServiceCardProps) {
  const [imageError, setImageError] = useState(false);
  
  // استخدام صورة احتياطية عند فشل تحميل الصورة الأصلية
  const handleImageError = () => {
    setImageError(true);
  };

  // توليد صورة احتياطية مع نص وخلفية ملونة
  const generatePlaceholder = () => {
    // تحديد لون الخلفية بناءً على التصنيف
    let bgColor = '#4f46e5'; // لون افتراضي (indigo)
    
    switch (category) {
      case 'image-generation':
      case 'static-story':
        bgColor = '#4338ca'; // indigo-700
        break;
      case 'video-generation':
      case 'animated-story':
        bgColor = '#be123c'; // rose-700
        break;
      case 'audio-generation':
        bgColor = '#0284c7'; // sky-700
        break;
      case 'text-generation':
      case 'article-generation':
      case 'research-generation':
      case 'deep-research':
        bgColor = '#15803d'; // green-700
        break;
      case 'website-app':
      case 'code-generation':
        bgColor = '#7c3aed'; // violet-600
        break;
      case 'model-training':
      case 'comfy-ui':
        bgColor = '#ca8a04'; // yellow-600
        break;
    }
    
    // إنشاء صورة SVG كنص
    return `data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%" viewBox="0 0 600 400" fill="none"><rect width="600" height="400" fill="${bgColor.replace('#', '%23')}" /><text x="50%" y="50%" font-family="Arial" font-size="24" fill="white" text-anchor="middle" dominant-baseline="middle">${title}</text></svg>`;
  };

  return (
    <div className="group bg-white rounded-lg shadow-sm overflow-hidden hover:shadow-md transition-shadow">
      <div className="relative h-48">
        {!imageError ? (
          <Image
            src={image}
            alt={title}
            fill
            sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw"
            className="object-cover group-hover:scale-105 transition-transform duration-300"
            onError={handleImageError}
          />
        ) : (
          <div 
            className="w-full h-full flex items-center justify-center bg-gradient-to-br from-blue-600 to-indigo-700 text-white font-medium text-center p-4"
            dangerouslySetInnerHTML={{ __html: `
              <svg xmlns="http://www.w3.org/2000/svg" class="h-12 w-12 mx-auto mb-2 opacity-80" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="1.5" d="${getCategoryIcon(category)}" />
              </svg>
              <div class="text-sm">${title}</div>
            `}}
          />
        )}
        
        <div className="absolute top-2 right-2">
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
            {getCategoryName(category)}
          </span>
        </div>
      </div>
      
      <div className="p-4">
        <Link href={`/services/${id}`}>
          <h3 className="text-lg font-medium text-gray-900 line-clamp-1 group-hover:text-indigo-600 transition-colors">
            {title}
          </h3>
        </Link>
        
        <p className="mt-1 text-sm text-gray-500 line-clamp-2">
          {description}
        </p>
        
        {provider && (
          <div className="mt-4 flex items-center">
            <div className="flex-shrink-0">
              <Link href={`/providers/${provider.id}`}>
                <div className="relative h-8 w-8 rounded-full overflow-hidden">
                  <Image
                    src={provider.image || '/images/avatar.jpg'}
                    alt={provider.name}
                    fill
                    className="object-cover"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.src = '/images/avatar.jpg';
                    }}
                  />
                </div>
              </Link>
            </div>
            <div className="mr-3">
              <Link href={`/providers/${provider.id}`} className="text-sm font-medium text-gray-900 hover:text-indigo-600">
                {provider.name}
              </Link>
              
              {rating !== undefined && (
                <div className="flex items-center text-sm text-gray-500">
                  <div className="flex items-center mr-1">
                    {Array.from({ length: 5 }).map((_, i) => (
                      <svg
                        key={i}
                        className={`h-3.5 w-3.5 ${i < Math.floor(rating) ? 'text-yellow-400' : 'text-gray-300'}`}
                        fill="currentColor"
                        viewBox="0 0 20 20"
                      >
                        <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                      </svg>
                    ))}
                  </div>
                  <span>{rating} {reviewsCount && `(${reviewsCount})`}</span>
                </div>
              )}
            </div>
          </div>
        )}
        
        <div className="mt-4 flex items-center justify-between">
          <div className="text-lg font-medium text-gray-900">
            {price} ر.س
          </div>
          <Link
            href={`/service-request?service=${id}`}
            className="inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
          >
            طلب الخدمة
          </Link>
        </div>
      </div>
    </div>
  );
}

// دالة لتحويل معرّف التصنيف إلى اسم مقروء
function getCategoryName(category: string): string {
  const categoryNames: Record<string, string> = {
    'image-generation': 'توليد صور',
    'video-generation': 'توليد فيديو',
    'audio-generation': 'توليد صوت',
    'text-generation': 'توليد نصوص',
    'article-generation': 'كتابة مقالات',
    'static-story': 'قصص ثابتة',
    'animated-story': 'قصص متحركة',
    'research-generation': 'أبحاث',
    'deep-research': 'أبحاث متعمقة',
    'website-app': 'مواقع وتطبيقات',
    'code-generation': 'برمجة وأكواد',
    'model-training': 'تدريب نماذج',
    'comfy-ui': 'Comfy UI'
  };
  
  return categoryNames[category] || category;
}

