// تعريف الفئات المتاحة للتصفية
const categories = [
  { id: 'all', name: 'جميع الخدمات' },
  { id: 'image-generation', name: 'توليد صور' },
  { id: 'video-generation', name: 'توليد فيديو' },
  { id: 'audio-generation', name: 'توليد صوت' },
  { id: 'article-generation', name: 'كتابة محتوى' },
  { id: 'research-generation', name: 'أبحاث' },
  { id: 'website-app', name: 'مواقع وتطبيقات' },
  { id: 'code-generation', name: 'برمجة' },
  { id: 'model-training', name: 'تدريب نماذج' }
];

interface ServicesGridProps {
  initialServices?: Service[];
  title?: string;
  showSearch?: boolean;
  showCategories?: boolean;
  selectedCategory?: string;
  maxItems?: number;
}

export default function ServicesGrid({
  initialServices,
  title = 'خدماتنا',
  showSearch = true,
  showCategories = true,
  selectedCategory = 'all',
  maxItems
}: ServicesGridProps) {
  const [services, setServices] = useState<Service[]>(initialServices || defaultServices);
  const [activeCategory, setActiveCategory] = useState(selectedCategory);
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  
  // محاكاة جلب البيانات من واجهة برمجة التطبيقات
  useEffect(() => {
    const fetchServices = async () => {
      setIsLoading(true);
      
      try {
        // في تطبيق حقيقي، سنقوم بإرسال طلب إلى واجهة برمجة التطبيقات
        // const response = await fetch('/api/services', {
        //   method: 'GET',
        //   headers: {
        //     'Content-Type': 'application/json',
        //   },
        // });
        
        // if (!response.ok) {
        //   throw new Error('فشل في جلب الخدمات');
        // }
        
        // const data = await response.json();
        // setServices(data.services);
        
        // محاكاة تأخير الشبكة
        setTimeout(() => {
          setServices(initialServices || defaultServices);
          setIsLoading(false);
        }, 800);
      } catch (error) {
        console.error('خطأ في جلب الخدمات:', error);
        setIsLoading(false);
      }
    };
    
    fetchServices();
  }, [initialServices]);
  
  // تصفية الخدمات حسب الفئة والبحث
  const filteredServices = services.filter(service => {
    const matchesCategory = activeCategory === 'all' || service.category === activeCategory;
    const matchesSearch = service.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      service.description.toLowerCase().includes(searchQuery.toLowerCase());
    
    return matchesCategory && matchesSearch;
  });
  
  // لعرض عدد محدد من الخدمات إذا تم تحديد ذلك
  const displayedServices = maxItems ? filteredServices.slice(0, maxItems) : filteredServices;
  
  return (
    <div>
      <div className="mb-8">
        {title && <h2 className="text-2xl font-bold text-gray-900 mb-4">{title}</h2>}
        
        {showSearch && (
          <div className="mb-6">
            <div className="relative max-w-lg">
              <div className="absolute inset-y-0 right-0 flex items-center pr-3 pointer-events-none">
                <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" aria-hidden="true" />
              </div>
              <input
                type="text"
                placeholder="ابحث عن خدمة..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="block w-full rounded-md border-gray-300 pr-10 focus:border-indigo-500 focus:ring-indigo-500 text-sm"
              />
            </div>
          </div>
        )}
        
        {showCategories && (
          <div className="flex flex-wrap gap-2 mb-6">
            {categories.map((category) => (
              <button
                key={category.id}
                onClick={() => setActiveCategory(category.id)}
                className={`px-4 py-2 text-sm font-medium rounded-full ${
                  activeCategory === category.id
                    ? 'bg-indigo-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {category.name}
              </button>
            ))}
          </div>
        )}
      </div>
      
      {isLoading ? (
        <div className="flex justify-center items-center py-12">
          <div className="inline-block h-8 w-8 animate-spin rounded-full border-4 border-solid border-indigo-500 border-r-transparent"></div>
          <span className="mr-4 text-gray-600">جاري تحميل الخدمات...</span>
        </div>
      ) : displayedServices.length > 0 ? (
        <div className="grid grid-cols-1 gap-y-10 gap-x-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
          {displayedServices.map((service) => (
            <ServiceCard
              key={service.id}
              id={service.id}
              title={service.title}
              description={service.description}
              image={service.image}
              category={service.category}
              price={service.price}
              provider={service.provider}
              rating={service.rating}
              reviewsCount={service.reviewsCount}
            />
          ))}
        </div>
      ) : (
        <div className="text-center py-12 bg-gray-50 rounded-lg">
          <p className="text-gray-600">لم يتم العثور على خدمات مطابقة للبحث</p>
          <button
            onClick={() => {
              setSearchQuery('');
              setActiveCategory('all');
            }}
            className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700"
          >
            عرض جميع الخدمات
          </button>
        </div>
      )}
      
      {maxItems && filteredServices.length > maxItems && (
        <div className="mt-8 text-center">
          <a
            href="/services"
            className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-indigo-700 bg-indigo-100 hover:bg-indigo-200"
          >
            عرض جميع الخدمات ({filteredServices.length})
          </a>
        </div>
      )}
    </div>
  );
} 